@include exports("dialog/theme") {

    .k-dialog {}

    // Titlebar
    .k-dialog-titlebar {
        @include fill(
            $dialog-titlebar-text,
            $dialog-titlebar-bg,
            $dialog-titlebar-border
        );
    }

    // Content scroll border
    .k-dialog-content.k-scroll {
        border-color: $component-border;
    }

}
